import { expose } from 'threads/worker';
import parseGcode from './parseGcode';
import { Subject, Observable } from 'threads/observable';

var _progress = new Subject();

expose({
  parse: function parse(gcode) {
    var moves = parseGcode(gcode, _progress);

    _progress.complete();

    _progress = new Subject();
    return moves;
  },
  progress: function progress() {
    return Observable.from(_progress);
  }
});